.App {
  font-family: sans-serif;
  text-align: center;
  padding: 20px;
}
.form {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  justify-content: center;
  margin-bottom: 20px;
}
table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 10px;
}
th, td {
  border: 1px solid #ccc;
  padding: 6px;
}
button {
  background-color: #0d6efd;
  color: white;
  border: none;
  padding: 8px 12px;
  cursor: pointer;
  border-radius: 6px;
}
button:hover {
  background-color: #0b5ed7;
}
